@import "colors";


footer {
    background-color: $fourth;
    height: auto;
    width: 100%;
    color: $first;
    display: grid;
    grid-template-columns: 4fr 1fr;
    padding: 2rem;

    >div {
        display: flex;
        flex-direction: column;
        justify-content: center;

        //align-items: flex-end;
        >h1 {
            font: 700 2rem $text1;
            //text-transform: uppercase;
            color: rgb(212, 14, 14);
        }

        >p {
            font: 500 1.2rem $text1;
            color: black;
        }

        >h5 {
            font: 300 2rem $text1;
            margin: 1rem 0;
            text-align: center;
            color: black;
        }

        >div {
            display: flex;
            flex-direction: column;
            align-items: center;

            a {
                color: $first;
                font: 900 1rem $text2;

                &:hover {
                    color: $fourth;
                }
            }
        }
    }
}