@import "colors";

.home {

    width: 100%;
    height: 100vh;
    background-color: $fourth;
    border-radius: 0 0 200px 200px;

    >main {
        position: absolute;
        //text-transform: uppercase;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        width: 100%;
        height: 100%;
        padding: 2rem;
        justify-content: center;

        h1 {
            font: 900 9rem $text1;
            color: $fourth;
        }

        p {
            font: 900 1rem $text1;
            color: rgb(212, 14, 14);
            text-transform: uppercase;
        }
    }

    &::before {
        content: "";
        width: 100%;
        height: 100%;
        position: absolute;
        background: no-repeat center/auto url("../assets/image052.png");
        filter: blur(0px);
        border-radius: 100px 100px 100px 100px;
    }
}

.home2 {
    width: 100%;
    height: 10vh;
    background-color: $fourth;
    padding: 205px 0;
    display: flex;
    align-items: center;
    align-content: center;
    margin-top: -200px;
}

.home3 {
    width: 100%;
    height: 150vh;
    background-color: $fourth;
    margin-top: -200px;
    display: flex;

    >div {
        width: 70%;
        height: 140vh;
        background-color: black;
        border-radius: 0 200px 200px 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 2rem;

        >h1 {
            font-size: 60px;
            text-transform: uppercase;
            border-bottom: 2px solid $first;
            padding: 0.5rem;
            width: 20rem;
            color: $first;
        }

        >p {
            color: $first;
            padding: 5rem;
            letter-spacing: 1px;
            word-spacing: 5px;
            line-height: 170%;
            font: italic 100 1.2rem $text2;
        }
    }
}

.home4 {
