@import "colors";

nav {
    width: 100%;
    padding: 0rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: sticky;
    background-color: $fourth;
    top: 0%;
    z-index: 10;



> main {
    width: 100%;
    display: flex;
    justify-content: flex-end;

    > a {
        color: $dark;
        margin: 1rem;
        
        > h1{
            text-transform: uppercase;
        }

        &:hover{
            color: $third;
        }
    }
}

}